body {
  margin: 0;
  padding: 0;
  font-family: "Monserat", sans-serif;
  background: linear-gradient(89deg, #3c4de7 0%, #e7709a 100%);
}

button {
  cursor: pointer;
}

button:focus {
  outline: none;
}

button:active {
  outline: none;
}

.bounding-box {
  position: absolute;
  box-shadow: 0 0 0 3px #149df2 inset;
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  cursor: pointer;
}

.Tilt {
  /* background: url(./img/logo1.png) center no-repeat; */
  background: linear-gradient(49deg, #e63232 0%, #17e3f1 100%);
}

.form {
  width: 700px;
  background: linear-gradient(27deg, #151515 5px, transparent 5px) 0 5px,
    linear-gradient(207deg, #151515 5px, transparent 5px) 10px 0px,
    linear-gradient(27deg, #222 5px, transparent 5px) 0px 10px,
    linear-gradient(207deg, #222 5px, transparent 5px) 10px 5px,
    linear-gradient(90deg, #1b1b1b 10px, transparent 10px),
    linear-gradient(
      #1d1d1d 25%,
      #1a1a1a 25%,
      #1a1a1a 50%,
      transparent 50%,
      transparent 75%,
      #242424 75%,
      #242424
    );
  background-color: #131313;
  background-size: 20px 20px;
}

.App {
  text-align: center;
  /* margin-bottom: 300px; */
}

.container {
  margin-top: -170px;
}

.center {
  display: flex;
  justify-content: center;
}

.particles {
  position: fixed;
  top: 0;
  right: 0;
  left: 0;
  bottom: 0;
  z-index: -1;
}

